<template>
  <div id="app" align="center">
    <input list="cars" @keyup.enter="drawPie(pmname)" v-model="pmname" placeholder="短横线'-'后请输入英文,例如:胡地-Mega,洛托姆-Wash" size="42">
    <datalist id="cars">
      <option v-for='item in compare' :key='item[0]'>
        {{item[2]}}
      </option>
      <option v-for='item in compare' :key='1000 + item[0]'>
        {{item[1]}}
      </option>
    </datalist>
    <button @click="teammates(pmname)">查询</button>
    <br>
    <input v-model="radioVal" type="radio" value="G1" @change="teammates(pmname)">G1
    <input v-model="radioVal" type="radio" value="G2" @change="teammates(pmname)">G2
    <input v-model="radioVal" type="radio" value="G3" @change="teammates(pmname)">G3
    <input v-model="radioVal" type="radio" value="G4" @change="teammates(pmname)">G4
    <input v-model="radioVal" type="radio" value="G5" @change="teammates(pmname)">G5
    <input v-model="radioVal" type="radio" value="G6" @change="teammates(pmname)">G6
    <input v-model="radioVal" type="radio" value="G7" @change="teammates(pmname)">G7
    <br>
    <p><img :src="imgUrl(pname)" v-if="pagesum > 0">{{ pname + '----' + ctoe(pname)}}</p>
    <ul id="list">
      <li v-for="(tm,index) in totalpage[currentpage-1]" :key='index' style="list-style-type:none;">
        <img :src="imgUrl(Object.keys(tm)[0])">
        {{ etoc(Object.keys(tm)[0]) }}----{{ tm[Object.keys(tm)[0]]+"%" }}----{{ Object.keys(tm)[0] }}
      </li>
    </ul>
    <button v-if="currentpage>1" @click="currentpage--">上一页</button>
    <span>{{currentpage}}</span>/<span>{{pagesum}}</span>
    <button v-if="currentpage<pagesum" @click="currentpage++">下一页</button>
  </div>
</template>

<script>
export default {
  created () {
    var that = this
    this.$axios.get('./compare.json').then(function (ret) {
      that.compare = ret.data.data
    })
  },
  methods: {
    teammates (pmname) {
      var that = this
      pmname = this.ctoe(pmname)
      // this.$axios.get('/Tm/' + that.radioVal + '/' + pmname).then(function (ret) {
      this.$axios.get(that.radioVal + 'tm.json').then(function (ret) {
        var a = []
        for (const i in ret.data[pmname]) {
          const o = {}
          o[i] = ret.data[pmname][i]
          a.push(o)
        }
        that.pmlist = a
        // 根据后台数据的条数和每页显示数量算出一共几页,得0时设为1 ;
        that.pagesum = Math.ceil(that.pmlist.length / that.pageSize) || 1
        for (let i = 0; i < that.pagesum; i++) {
          // 每一页都是一个数组 形如 [['第一页的数据'],['第二页的数据'],['第三页数据']]
          // 根据每页显示数量 将后台的数据分割到 每一页,假设pageSize为5， 则第一页是1-5(对slice操作来说是0-5)条，第二页是6-10(对slice操作来说是5-10)条...
          that.totalpage[i] = that.pmlist.slice(that.pageSize * i, that.pageSize * (i + 1))
        }
      })
      this.pname = this.pmname
      this.currentpage = 1
    },
    ctoe (pmname) {
      // 输入的中文转英文
      if (pmname.indexOf('-') >= 0) {
        let pmname1 = pmname.substring(0, pmname.indexOf('-'))
        this.compare.map(e => {
          if (e[2] === pmname1) {
            pmname1 = e[1]
          }
        })
        pmname = pmname1 + pmname.substring(pmname.indexOf('-'), pmname.length)
      } else {
        this.compare.map(e => {
          if (e[2] === pmname) {
            pmname = e[1]
          }
        })
      }
      return pmname
    },
    etoc (pmname) {
      // 输入的英文转中文
      if (pmname.indexOf('-') >= 0) {
        let pmname1 = pmname.substring(0, pmname.indexOf('-'))
        this.compare.map(e => {
          if (e[1] === pmname1) {
            pmname1 = e[2]
          }
        })
        pmname = pmname1 + pmname.substring(pmname.indexOf('-'), pmname.length)
      } else {
        this.compare.map(e => {
          if (e[1] === pmname) {
            pmname = e[2]
          }
        })
      }
      return pmname
    },
    // ps gif图片
    // imgUrl (name) {
    //   var purename = name.toLowerCase().replace(/[&|*^%$#@: ']/g, '')
    //   if (purename === 'ho-oh' || purename === 'kommo-o' || purename === 'jangmo-o' || purename === 'hakamo-o') {
    //     purename = purename.replace(/-/g, '')
    //   } else if (purename === 'charizard-mega-x') {
    //     purename = 'charizard-megax'
    //   } else if (purename === 'charizard-mega-y') {
    //     purename = 'charizard-megay'
    //   }
    //   return 'https://play.pokemonshowdown.com/sprites/ani/' + purename + '.gif'
    // },
    // pmgba icon
    imgUrl (name) {
      var index = ''
      var flag = 0
      if (name.indexOf('-Mega-X') >= 0) {
        name = name.substring(0, name.length - 7)
        flag = 1
      } else if (name.indexOf('-Mega-Y') >= 0) {
        name = name.substring(0, name.length - 7)
        flag = 2
      } else if (name.indexOf('-Mega') >= 0) {
        name = name.substring(0, name.length - 5)
        flag = 1
      } else if (name.indexOf('-Alola') >= 0) {
        name = name.substring(0, name.length - 6)
        flag = 1
      } else if (name.indexOf('-Therian') >= 0) {
        name = name.substring(0, name.length - 8)
        flag = 1
      } else if (name.indexOf('-Unbound') >= 0) {
        name = name.substring(0, name.length - 8)
        flag = 1
      } else if (name.indexOf('-Super') >= 0) {
        name = name.substring(0, name.length - 6)
        flag = 1
      } else if (name.indexOf('-Dusk') >= 0) {
        name = name.substring(0, name.length - 5)
        flag = 2
      } else if (name.indexOf('-Midnight') >= 0) {
        name = name.substring(0, name.length - 9)
        flag = 1
      } else if (name.indexOf('-Ash') >= 0) {
        name = name.substring(0, name.length - 4)
        flag = 2
      } else if (name.indexOf('-Black') >= 0) {
        name = name.substring(0, name.length - 6)
        flag = 2
      } else if (name.indexOf('-Fan') >= 0) {
        name = name.substring(0, name.length - 4)
        flag = 3
      } else if (name.indexOf('-Frost') >= 0) {
        name = name.substring(0, name.length - 6)
        flag = 4
      } else if (name.indexOf('-Heat') >= 0) {
        name = name.substring(0, name.length - 5)
        flag = 1
      } else if (name.indexOf('-Mow') >= 0) {
        name = name.substring(0, name.length - 4)
        flag = 5
      } else if (name.indexOf('-Wash') >= 0) {
        name = name.substring(0, name.length - 5)
        flag = 2
      } else if (name.indexOf('-Bug') >= 0) {
        name = name.substring(0, name.length - 4)
      } else if (name.indexOf('-Dark') >= 0) {
        name = name.substring(0, name.length - 5)
      } else if (name.indexOf('-Dragon') >= 0) {
        name = name.substring(0, name.length - 7)
      } else if (name.indexOf('-Electric') >= 0) {
        name = name.substring(0, name.length - 9)
      } else if (name.indexOf('-Fairy') >= 0) {
        name = name.substring(0, name.length - 6)
      } else if (name.indexOf('-Fighting') >= 0) {
        name = name.substring(0, name.length - 9)
      } else if (name.indexOf('-Fire') >= 0) {
        name = name.substring(0, name.length - 5)
      } else if (name.indexOf('-Flying') >= 0) {
        name = name.substring(0, name.length - 7)
      } else if (name.indexOf('-Ghost') >= 0) {
        name = name.substring(0, name.length - 6)
      } else if (name.indexOf('-Grass') >= 0) {
        name = name.substring(0, name.length - 6)
      } else if (name.indexOf('-Ground') >= 0) {
        name = name.substring(0, name.length - 7)
      } else if (name.indexOf('-Ice') >= 0) {
        name = name.substring(0, name.length - 4)
      } else if (name.indexOf('-Poison') >= 0) {
        name = name.substring(0, name.length - 7)
      } else if (name.indexOf('-Psychic') >= 0) {
        name = name.substring(0, name.length - 8)
      } else if (name.indexOf('-Rock') >= 0) {
        name = name.substring(0, name.length - 5)
      } else if (name.indexOf('-Steel') >= 0) {
        name = name.substring(0, name.length - 6)
      } else if (name.indexOf('-Water') >= 0) {
        name = name.substring(0, name.length - 6)
      } else if (name.indexOf('-Small') >= 0) {
        name = name.substring(0, name.length - 6)
      } else if (name.indexOf('-Large') >= 0) {
        name = name.substring(0, name.length - 6)
      } else if (name.indexOf('-Totem') >= 0) {
        name = name.substring(0, name.length - 6)
      } else if (name.indexOf('-50%') >= 0) {
        name = name.substring(0, name.length - 4)
      } else if (name.indexOf('-10%') >= 0) {
        name = name.substring(0, name.length - 4)
        flag = 1
      } else if (name.indexOf('-Sandy') >= 0) {
        name = name.substring(0, name.length - 6)
        flag = 1
      } else if (name.indexOf('-Trash') >= 0) {
        name = name.substring(0, name.length - 6)
        flag = 2
      } else if (name.indexOf('-Pom-Pom') >= 0) {
        name = name.substring(0, name.length - 8)
        flag = 1
      } else if (name.indexOf('-Sensu') >= 0) {
        name = name.substring(0, name.length - 6)
        flag = 3
      } else if (name.indexOf('-Pa\'u') >= 0) {
        name = name.substring(0, name.length - 5)
        flag = 2
      }
      this.compare.map(e => {
        if (e[1] === name || e[2] === name) {
          index = e[0]
        }
      })
      if (flag !== 0) {
        return 'http://res.pokemon.name/common/pokemon/icons/' + index + '.0' + flag + '.png'
      } else {
        return 'http://res.pokemon.name/common/pokemon/icons/' + index + '.00.png'
      }
    }
  },
  data () {
    return {
      pmname: '',
      pname: '',
      pmlist: [],
      radioVal: 'G7',
      currentpage: 1,
      pagesum: 0,
      totalpage: [],
      pageSize: 10,
      compare: []
    }
  }
}
</script>
