      // 输入的中文转英文
      if (pmname.indexOf('-') >= 0) {
        let pmname1 = pmname.substring(0, pmname.indexOf('-'))
        this.compare.map(e => {
          if (e[2] === pmname1) {
            pmname1 = e[1]
          }
        })
        pmname = pmname1 + pmname.substring(pmname.indexOf('-'), pmname.length)
      } else {
        this.compare.map(e => {
          if (e[2] === pmname) {
            pmname = e[1]
          }
        })
      }
      return pmname
    },
